import React from 'react';
import {Link} from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { putPurchase } from 'js/redux/actions/purchase';
import { closeModal } from 'js/redux/actions/container';

import FloatInput from 'js/components/validation/FloatInput';
import IntInput from 'js/components/validation/IntInput';
import TextInput from 'js/components/validation/TextInput';
import Modal from 'js/components/common/Modal';

import { getColClass, getOffsetClass } from 'js/util/styleUtil';

class PurchaseModal extends React.Component {
    render() {
        const { dispatch, target } = this.props;
        return (
            <Modal
                title = {
                <span>
                    <h4 className = 'modal-title'></h4>
                    <h5 className = 'modal-title'>:<small>{target.materialName}</small></h5>
                    <h5 className = 'modal-title'>:<small>{target.spName}</small></h5>
                </span>
                }
                onCloseClick = {() => dispatch(closeModal())}
                onFormSubmit = {event => dispatch(putPurchase(target, event.target))}
                >
                <div className = 'form-group'>
                    <label className = {getColClass(4, 4, getOffsetClass(2, 2, 'control-label'))}>
                        :
                    </label>

                    <div className = {getColClass(9, 9, getOffsetClass(1, 1))}>
                        <FloatInput
                            name = 'unitPrice'
                            placeholder = ''
                            notFloatMsg = ''
                            emptyMsg = ''
                            outMaxRangeMsg = '...'
                            outMinRangeMsg = '0'
                            max = {999999999.0}
                            min = {0}
                            required
                            />
                    </div>
                </div>
                <div className = 'form-group'>
                    <label className = {getColClass(4, 4, getOffsetClass(2, 2, 'control-label'))}>
                        :
                    </label>

                    <div className = {getColClass(9, 9, getOffsetClass(1, 1))}>
                        <IntInput
                            name = 'purchaseAmount'
                            placeholder = ''
                            notIntMsg = ''
                            emptyMsg = ''
                            outMaxRangeMsg = '..'
                            outMinRangeMsg = '0'
                            max = {999999}
                            min = {0}
                            required
                            />
                    </div>
                </div>
                <div className = 'form-group'>
                    <label className = {getColClass(4, 4, getOffsetClass(2, 2, 'control-label'))}>
                        :
                    </label>

                    <div className = {getColClass(9, 9, getOffsetClass(1, 1))}>
                        <FloatInput
                            name = 'totalCost'
                            placeholder = ''
                            notFloatMsg = ''
                            emptyMsg = ''
                            outMaxRangeMsg = '...'
                            outMinRangeMsg = '0'
                            max = {999999999.0}
                            min = {0}
                            required
                            />
                    </div>
                </div>
                <div className = 'form-group'>
                    <label className = {getColClass(4, 4, getOffsetClass(2, 2, 'control-label'))}>
                        :
                    </label>

                    <div className = {getColClass(9, 9, getOffsetClass(1, 1))}>
                        <TextInput
                            name = 'supplier'
                            placeholder = ''
                            emptyMsg = ''
                            outRangeMsg = '45'
                            maxLength = '45'
                            required
                            />
                    </div>
                </div>
            </Modal>
        );
    }
}

export default connect(state => ({}))(PurchaseModal);


